html {
  box-sizing: border-box;
  max-width: 1280px;
  margin: 0 auto;
  padding-inline: 55px;
}

body {
  box-sizing: border-box;
  margin: 0;
}

.header {
  box-sizing: border-box;
  max-width: 1280px;
  height: 100vh;
}

.header__content {
  grid-template-rows: 1fr 3fr 2fr;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;
  display: grid;
  position: relative;
}

.header__main {
  grid-row: 2 / 2;
  align-self: center;
}

.header__top-bar {
  grid-column: 1 / 1;
  margin-top: 24px;
}

.header__top-bar__menu {
  background-image: url("header-menu-24.71e17b40.png");
  width: 24px;
  height: 24px;
}

.header__logo {
  text-align: center;
  z-index: 2;
  grid-column: 6 / 7;
  margin: 0;
  padding: 0;
  font-family: Montserrat, serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 36px;
  display: inline;
  position: absolute;
  left: 50%;
  right: 50%;
}

.header__image {
  z-index: 1;
  background-image: url("header-pc.c3dfb68f.png");
  background-repeat: no-repeat;
  background-size: contain;
  grid-area: 1 / 6 / -1 / -1;
  width: 725px;
  height: 655px;
}

.header__title {
  margin: 0;
  padding: 0;
  font-family: Montserrat, serif;
  font-size: 72px;
  font-weight: 700;
  line-height: 86px;
}

.header__tickets {
  border: 1px, solid, black;
  grid-row: 3 / 3;
  justify-content: end;
  align-items: center;
  width: 270px;
  height: 70px;
  display: flex;
}

.header__tickets--title {
  margin: auto;
  padding: 0;
  font-family: IBM Plex Sans, serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;
}

.header__tickets__button {
  background-color: #1a5a4c;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 100%;
  display: flex;
}

.header__tickets__button--arrow {
  color: #fff;
}
